$menu-item-color: #6b5b95;

.logo {
    font-size: 40px;
    font-family: 'Open Sans', sans-serif; 
    font-weight: normal;
    width: 100%;
    color: #111;
    &:hover {
        text-shadow: 1px 1px 2px white, 0 0 25px #92a8d1;
        -webkit-transition: text-shadow ease 1s; /* Safari */
        transition: text-shadow ease 1s;
        // box-shadow: 1px ,1px , 1px , 1px, blue;
    }
}

.row {
    display: flex;
    flex-wrap: wrap;
}

.center {
    text-align: center;
}



nav {
    text-align: center;
    font-family: 'Roboto', sans-serif;
    ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        li {
            display: inline;
            padding-right: 5px;
            color: #111;
            margin: 15px;

            &:last-of-type {
                padding-right: 0;
            }

            &:hover,
            &:focus {
                color: #6b5b95;
                font-weight: bold;
            }

            .fa { 
                color: $menu-item-color;
                visibility: hidden;
            }       
            &:hover .fa {
                visibility: visible;
            }   
        }
    }
    a {
        text-decoration: none;
         &:focus {
            color: #6b5b95;
            font-weight: bold;
        }
    }
}
